import cTitle from 'components/title';
export default
  {
    data() {
      return {
        balance: 0

      }
    },
    methods:
    {
      //充值的响应方法
      charge() {
        //this.$router.push('/member/balance_recharge');
        this.$router.push({ name: "balance_recharge", params: { balance: this.balance } });
      },
      //转账的响应方法
      transfer() {
        //this.$router.push('/member/balance_transfer');
        this.$router.push({ name: "balance_transfer", params: { balance: this.balance } });
      },
      //提现的响应方法
      withdraw() {
        //this.$router.push('/member/balance_withdrawals');
        this.$router.push({ name: "balance_withdrawals", params: { balance: this.balance } });
      },
      getBalance() {
        var that = this;
        $http.get('finance.balance.balance').then(function (response) {

          if (response.result == 1) {

            console.log(response.data);
            that.balance = response.data;
          }

        }, function (response) {
          // error callback
        });

      }

    },
    activated() {
      this.getBalance();
      //this.balance = this.$route.params.balance_value;

    },
    components: { cTitle }
  }


